// @ts-nocheck

import React from 'react';
import RenSDK from "@renproject/ren";
import GatewayJS from "gateway-js";
import Web3 from "web3";
import BigNumber from "bignumber.js";
import './App.css';

import ABI from "./ABI.json";
import ERC20ABI from "./ERC20ABI.json";
import ShifterRegistryABI from "./ShifterRegistryABI.json";

// Replace with your contract's address.
const shifterRegistryAddress = "0xbA563a8510d86dE95F5a50007E180d6d4966ad12";
const contractAddress = "0xa2aE9111634F5983e4e1C3E3823914841a4c7235";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      balance: 0,
      message: "",
      sdk: new RenSDK("testnet"),
    }
  }

  async componentDidMount() {
    let web3Provider;

    // Initialize web3 (https://medium.com/coinmonks/web3-js-ethereum-javascript-api-72f7b22e2f0a)
    // Modern dApp browsers...
    if (window.ethereum) {
      web3Provider = window.ethereum;
      try {
        // Request account access
        await window.ethereum.enable();
      } catch (error) {
        // User denied account access...
        console.error("User denied account access")
      }
    }
    // Legacy dApp browsers...
    else if (window.web3) {
      web3Provider = window.web3.currentProvider;
    }
    // If no injected web3 instance is detected, fall back to Ganache
    else {
      this.log("Please install MetaMask!");
    }

    const web3 = new Web3(web3Provider);

    this.setState({ web3 }, () => {

      // Update balances immediately and every 10 seconds
      this.updateBalance();
      setInterval(() => {
        this.updateBalance();
      }, 10 * 1000);
    });
  }

  render() {
    const { balance, message } = this.state;
    return (
      <div className="App">
        <h1>RenVM demo</h1>
        <p><b>This demo is on <i>testnet</i>. Do not send real funds.</b></p>
        <p>Balance: <b>{balance} BTC</b> on Ethereum</p>
        <p><button onClick={this.deposit}>Deposit 0.00015 BTC</button></p>
        <p><button onClick={this.withdraw}>Withdraw 0.00015 BTC</button></p>
        <p>{message}</p>
      </div>
    );
  }

  updateBalance = async () => {
    const { web3 } = this.state;
    const contract = new web3.eth.Contract(ABI, contractAddress);

    const address = (await web3.eth.getAccounts())[0];

    const balance = await contract.methods.balance(address).call();
    this.setState({ balance: parseInt(balance.toString()) / 10 ** 8 });
  }

  log = (message) => {
    this.setState({ message });
  }

  deposit = async () => {
    console.log("inside deposit!");

    const { web3, sdk } = this.state;
    const amount = new BigNumber("0.00015"); // BTC

    const address = (await web3.eth.getAccounts())[0];
    console.log(`Using address ${address}!!!`);

    const response = await new GatewayJS().open({
      // Send BTC from the Bitcoin blockchain to the Ethereum blockchain.
      sendToken: RenSDK.Tokens.BTC.Btc2Eth,

      // Amount of BTC we are sending (in Satoshis)
      sendAmount: amount.times(10 ** 8).toNumber(), // Convert to Satoshis

      // The contract we want to interact with
      sendTo: contractAddress,

      // The name of the function we want to call
      contractFn: "shiftIn",

      // Arguments expected for calling `deposit`
      contractParams: [
        {
          name: "_to",
          type: "address",
          value: address,
        }
      ],
    }).catch((error) => { console.log(error); this.log(error.message || String(error)) });

    this.log(JSON.stringify(response));
  }

  withdraw = async () => {
    const { web3, sdk, balance } = this.state;

    const amount = 0.00015; // balance;
    // const recipient = prompt("Enter BTC recipient:");
    // if (!recipient) {
    //   return;
    // }
    const from = (await web3.eth.getAccounts())[0];
    // const contract = new web3.eth.Contract(ABI, contractAddress);

    // this.log("Calling `withdraw` on smart contract...");
    // const txHash = await new Promise((resolve, reject) => {
    //   contract.methods.withdraw(
    //     web3.utils.fromAscii(`Depositing ${amount} BTC`), // _msg
    //     RenSDK.Tokens.BTC.addressToHex(recipient), //_to
    //     amount * (10 ** 8), // _amount in Satoshis
    //   ).send({ from })
    //     .on("transactionHash", resolve)
    //     .catch(reject);
    // });

    const shifterRegistry = new web3.eth.Contract(ShifterRegistryABI, shifterRegistryAddress);
    const zbtc = await shifterRegistry.methods.getTokenBySymbol("zBTC").call();

    const token = new web3.eth.Contract(ERC20ABI, zbtc);
    await new Promise((resolve, reject) => {
      token.methods.approve(contractAddress, Math.floor(amount * 10 ** 8))
        .send({ from })
        .on("transactionHash", resolve)
        .catch(reject);
    }).catch((error) => this.log(error.message || String(error)));

    const response = await new GatewayJS().open({
      // Send BTC from the Ethereum blockchain to the Bitcoin blockchain.
      // This is the reverse of shitIn.
      sendToken: RenSDK.Tokens.BTC.Eth2Btc,

      // The contract we want to interact with
      sendTo: contractAddress,

      // The name of the function we want to call
      contractFn: "shiftOut",

      // Arguments expected for calling `deposit`
      contractParams: [
        {
          name: "_to",
          type: "bytes",
          value: GatewayJS.askForAddress(), // RenSDK.Tokens.BTC.addressToHex(recipient),
        },
        {
          name: "_to",
          type: "uint256",
          value: Math.floor(amount * 10 ** 8),
        }
      ],

      // // The transaction hash of our contract call
      // txHash,
    }).catch((error) => this.log(error.message || String(error)));

    this.log(JSON.stringify(response));

    // this.log(`Submitting to Darknodes...`);
    // await shiftOut.submitToRenVM();
    // this.log(`Withdrew ${amount} BTC to ${recipient}.`);
  }
}

export default App;
